import React, {Component} from 'react';
import {getPhotoByIdRole, logout} from "../redux/actions/register/client-register";

import "../assets/css/dashboard.css"
import {connect} from "react-redux";

class Header extends Component{
	constructor(){
		super();

		this.state = {
			account: '',
		}
	}

	componentDidMount(){
		const {
			getPhotoByIdRole
		} = this.props;
		getPhotoByIdRole({
			id: localStorage.client_id,
			role: 'client',
		});
	}

	componentDidUpdate(prevProps, prevState, snapshot){
		if(this.props.clientInfo && prevProps.clientInfo !== this.props.clientInfo){
			this.setState({
				account: this.props.clientInfo,
			})
		}
	}

	logOut = () => {
		const {
			logout
		} = this.props;

		let data = {
			id: localStorage.getItem('client_id'),
			role: 'client',
		};

		if(logout){
			logout(data, this.props.history);
		}
	};

	render(){
		return (
			<>
				<div className="flex-space header-space">
					<div className="rectangle-menu justify-center mouse-cursor" onClick={this.props.toggleSideMenu}>
						<img
							src={require(this.props.shownSideMenu ? '../assets/img/desktop-menu.svg' : '../assets/img/mobile-menu.svg')}
							alt=""/>
					</div>

					<div className="align-right txt-16 col-heavyDark justify-center">
						<div className="btn-header mouse-cursor " onClick={this.logOut}>
							<div className="flex-space">
								<div>Hi, <span className="txt-bold">{localStorage.getItem('client_name')}</span></div>
								<div>
									{/*<img src={require('../assets/img/account.svg')} alt="" />*/}
									<img
										className="photo-header"
										src={
											this.state.account.photo
												?
												this.state.account.photo
												:
												require('../assets/img/account.svg')}
										alt=""
									/>
								</div>
							</div>
						</div>
					</div>
				</div>
			</>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		clientInfo: state.registers.clientInfo,
	}
};

export default connect(
	mapStateToProps,
	{logout, getPhotoByIdRole}
)(Header);
